body{
    background-image: linear-gradient(90deg, rgb(237, 228, 100),rgb(252, 152, 51));
}
.HomePage{
    .TitleText{
        margin: 10px;
        .HeaderText{
            display: flex;
            justify-content: center;
            .navbar-text{
                color: aliceblue;
                font-size: larger;
            }
        }
    }
    .CardsContainer{
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 15px;
        margin-left: 10px;
        .card {
            width: 130px;
            height: 154px;
            background-image: linear-gradient(163deg, #00ff75 0%, #3700ff 100%);
            border-radius: 20px;
            transition: all .3s;
           }
           
           .card2 {
            width: 130px;
            height: 154px;
            background-color: #1a1a1a;
            border-radius:10px;
            transition: all .2s;


            .CardText{
                display: flex;
                justify-content: center;
                align-items: center;
                text-decoration: none;
                color: aliceblue;
                width: 100%;
                height: 100%;
                
            }
           }
           
           .card2:hover {
            transform: scale(0.98);
            border-radius: 20px;
            font-size: larger;
           }
           
           .card:hover {
            box-shadow: 0px 0px 30px 1px rgba(0, 255, 117, 0.30);
           }
    }
}

.Timer{

    .TimerHeader{ 
        background-color: cadetblue;
        text-align: center;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;

        .linkTag{
            background-color: rgb(127, 165, 198);
            padding: 10px;
            border: 0.5px solid black;
            width: 15%;
            // height: 50%;
            .link{
                text-decoration: none;
                color: black;
                padding: 12px 25px;
            }
            
        }
        
        .title{
            background-color: bisque;
            font-weight: bolder;
            font-family: 'Courier New', Courier, monospace;
            font-size: larger;
            padding: 10px;  
        }        
    }

    .ClockTimer{
        border: 2px solid black;
        margin-top: 20px;
        padding-bottom: 20px;
        .time{
            text-align: center;
            
            padding: 17px 0px;
            display: flex;
            justify-content: center;
            gap: 15px;
            
            .numberBox{
                border: 1px dashed black;
                display: flex;
                justify-content: center;
                gap: 13px;
                padding: 10px 30px;
                
                .numbers{
                    border: 1px dashed black;
                    padding: 5px 5px;
                    font-size: larger;
                }
                .selector{
                    width: 190px;
                    height: 30px;
                    color: aliceblue;
                    background-color: black;
                    border: 2px solid yellowgreen;
                }

                .inputs{
                    width: 15px;
                    height: 25px;
                }

                .inputs::-webkit-inner-spin-button{
                    display: none;
                }
            }
        }

        .buttons{
            display: flex;
            justify-content: center;
            gap: 15px;

            .btn{
                background-color: aliceblue;
                border: 0.5px solid black;
                border-radius: 5px;
                padding: 10px 30px;
                cursor: pointer;
            }
        }
    }
}

.calculatorProject{
    .header{
        display: flex;
        justify-content: center;
        margin: 10px;
    }

    .CalculatorContainer{
        display: flex;
        justify-content: center;
        padding-top: 40px;

        .calculator{
            display: grid;
            grid-template-columns: repeat(4,1fr);
            font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            font-weight: bold;

            .buttons{
                border: 1px solid black;
                margin: 1px;
                width: 70px;
                height: 60px;
            }
            .answerBox{
                border: 1px solid black;
                grid-column: 1/5;
                margin: 1px;
                height: 60px;
                display: flex;
                align-items: center;
                justify-content: end;
                padding-right: 10px;
            }
            .plusButton{
                grid-row: 3/5;
                grid-column: 4;
            }
            .equalButton{
                grid-row: 5/7;
                grid-column: 4;
            }
        }
    }
}